@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Display:wght@200;300;400;700&family=Noto+Sans:wght@300;400;700&family=Nunito+Sans:wght@200;300;400;700&display=swap');
:root {
    --transition: .75s cubic-bezier(.2, .6,0, 1);
    --ss: 2vw;
    --sm: 4vw;
    --vs: 5vmin;
    --vm: 8vmin;
    --vl: 10vmin;
    --top-space: 8vmin;
    --max-width: 1920px;
    --fs-xxs: 14px;
    --fs-xs: 16px;
    --fs-s: 1.2rem;
    --fs-m: 1.4rem;
    --fs-l: 2.8rem;
    --fs-xl: 3.2rem;
    --xxs: .6rem;
    --xs: 1.2rem;
    --s: 2.2rem;
    --m: 3rem;
    --l: 5rem;
    --xl: 8rem;
    --thin: #e5e5e5;
    --light-gray: #ddd;
    --w-col: 500px;
    --w-min-content: 120rem;
    --gray: #f1f1f1;
    --gray100: #e8e8e8;
    --gray200: #dbdbdb;
    --gray300: #a6a6a6;
    --gray400: #777;
    --gray500: #3a3a3a;
    --gray900: #151515;
    --black: #1f1f1f;
    --dark: #10080a;
    --red: #ED515A
}

body, h1, h2, h3, h4, h5, h6, p, ul, li, fieldset {
    margin: 0;
    padding: 0;
}
fieldset {
    border: 0;
}
h2 {
    font-size: var(--fs-m);
}

h3 {
    font-size: var(--fs-s);
}
*, :before, :after {
    box-sizing: border-box;
}

html {
    font-size: clamp(var(--fs-xs), calc((1vw + 1vmin)), 20px);
    line-height: 1.4;
}
body {
    font-family: 'Nunito Sans', sans-serif;
    color: var(--gray500);
    font-size: var(--fs-xs);
    height: 100vh;
}
#root {
    height: 100vh;
    overflow: hidden auto;
}
a {
    text-decoration: none;
    display: inline-block;
    color: inherit;
    cursor: pointer;
}
h1, h2, h3 {
    color: var(--gray900);
    font-weight: 400;
    letter-spacing: -0.005em;
    margin-bottom: .5em;
}
li {
    list-style-type: none;
}
button {
    display: flex;
    line-height: 1;
    font-size: var(--fs-xs);
    box-sizing: border-box;
    background-color: var(--gray100);
    color: inherit;
    align-items: center;
    cursor: pointer;
    padding: 0;
    border: 0;
}

input, button {
    font-family: inherit;
}

.scrollbar::-webkit-scrollbar {
     width: 7px;
 }

.scrollbar::-webkit-scrollbar-track {
     background-color: transparent;
 }

.scrollbar::-webkit-scrollbar-thumb {
     background-color: var(--gray200);
     border-radius: 1px;
 }



.relative {
    position: relative;
}
.hide{
    display: none;
}

p {
    margin-bottom: 1em;
}

.br {
    border-radius: 2px;
}
.b0 {
    border: 0;
}
.pn {
    padding: .8em;
}
[size="normal"] {
    padding: 0.8em;
}
[size="small"] {
    padding: 0.6em;
}
[size="mini"] {
    padding: 0.3em 0.4em;
}
.gap {
    gap: 10px;
}
.fx {
    display: flex;
    flex-wrap: wrap;
}
.fx-cm {
    flex-direction: column;
}
.fx-b {
    align-items: flex-end;
}
.jc-r {
    justify-content: flex-end;
}
.jc-sb {
    justify-content: space-between;
}
.container {
    position: relative;
    padding-left: var(--ss);
    padding-right: var(--ss);
}

.content {
    font-size: var(--fs-xs);
    line-height: 1.6;
    padding-top: var(--vs);
    padding-bottom: var(--vs);
}
.mw {
    max-width: var(--max-width);
    margin-left: auto;
    margin-right: auto;
}
.LstClose {
    cursor: pointer;
}
.LstClose:after {
    content: '';
    display: block;
    width: 14px;
    height: 14px;
    background-color: white;
    clip-path: polygon(10% 0, 0 10%, 40% 50%, 0 90%, 10% 100%, 50% 60%, 90% 100%, 100% 90%, 60% 50%, 100% 10%, 90% 0, 50% 40%);
}

.LstArrow:after {
    content: '';
    display: block;
    width: 14px;
    height: 14px;
    background-color: black;
    clip-path: polygon(30% 0, 20% 10%, 60% 50%, 20% 90%, 30% 100%, 80% 50%);
}